.Footer {
  /* border: 2px red solid; */
  position: absolute;
  display: table;
  width: 100vw;
  height:100px;
  padding-left: 0px;
  padding-bottom: 10px;
  padding-top: 10px;
  margin-top: 0px;
  margin-bottom: 0px;
  table-layout: fixed; /* For cells of equal size */
  background-color: White;
  bottom: 0px;
}

.Footer li {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}

.Footer img {
  cursor: pointer;
  transition: 200ms linear;
  opacity: 1;
}


ul li .active img {
  border-radius: 20px;
  background: linear-gradient(180deg, #e43117 0%, #70180b 75%, #000000 100%);
  box-shadow: 0px 5px 15px rgba(105, 61, 231, 0.2);
  padding: 25px;
  position: relative;
}
